.wish{
    float: right;
    width: 100%;
    position: relative;
}
.leftLine{
    width: 20px;
    height: 100%;
    position: absolute;
    float: left;
    margin-top: 27px;
    svg{
        position:absolute;
        height: 25px;
        width: 25px;
        right: 0px;
        top: 0px;
        transform: translate(50%, -50%);
    }
}
.circle{
    height: 7px;
    width: 7px;
    border-radius: 50%;
    background-color: rgb(200, 200, 200);
    position: absolute;
    right: -4px;
}
.isOnlyOne{
    .circle:last-child{
        visibility: hidden;
    }
}
.wishNotZero > .wish:not(:last-child) > .leftLine{
    border-right: 1px solid rgb(200, 200, 200);
    .circle:last-child{
        top: 100%;
    }
}
.wishContent{
    position: relative;
    float: right;
    background-color: white;
    width: 80%;
    border-radius: 5px;
    margin: 10px 15px 10px 0;
    padding: 10px 10px 20px 10px;
    .time{
        width: 100%;
        margin-bottom: 5px;
        font-size: 14px;
        color: grey;
    }
    .content{
        width: 100%;
        font-size: 18px;
        word-break: break-all;
    }
    .private{
        position: absolute;
        right: 5px;
        bottom: 5px;
        font-size: 12px;
        color: grey;
    }
}
.wishMenu{
    width: 170px;
    position: absolute;
    right: 17px;
    top: 5px;
    text-align: center;
    font-size: 16px;
    span{
        display: inline-block;
        width: 40px;
        color: white;
        background-color: rgb(30, 30, 30);
        opacity: 0.8;
        padding: 2px 2px 2px 2px;
        border-radius: 3px;
        margin-left: 10px;
    }
}