@import '../../global';

.wishListPage{
    @extend .pageStyle;
}
.wishList{
    height: 100%;
}

.wishHeader{
    @extend .headerStyle;
    position: sticky;
    top: 0px;
    background-color: $background-color;
    z-index: 10;
    text-align: center;
    .back{
        left: 10px;
        top: 7px;
        position: absolute;
        height: 30px;
        width: 30px;
    }
    .r{
        height: 12px;
        width: 12px;
        position: relative;
        top: -10px;
    }
}
.wishZero{
    width: 100%;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    img{
        height: 120px;
        width: 180px;
    }
}
.wishButton{
    background-color: red;
    border: none;
    border-radius: 50px;
    height: 50px;
    width: 200px;
    color: white;
    font-size: 20px;
    &:active{
        background-color: rgb(230, 0, 0);
        color: rgb(230, 230, 230);
    }
}
.wishNotZero{
    background-color: $background-color;
    overflow: scroll;
}
.height100{
    height: 95%;
    padding-bottom: 0%;
}
.height85{
    height: 82%;
    padding-bottom: 110px;
}
.wishBottom{
    position: fixed;
    bottom: 0px;
    left: 0px;
    height: 90px;
    background-color: white;
    width: 100%;
    button{
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -65%);
    }
}

.edit{
    @extend .pageStyle;
    overflow: hidden;
}
.editHeader{
    @extend .headerStyle;
    .cancel{
        position: absolute;
        left: 20px;
    }
    .ensure{
        position: absolute;
        right: 20px;
    }
}
.editAre{
    border:none;
	outline: none;
	resize: none;
    width: 373px;
    height: 100%;
    background-color: $background-color;
    font-size: 20px;
    margin-top: 20px;
    padding: 0 20px 20px 20px;
    overflow: hidden;
}
.buttonPrevent{
    pointer-events: none;
    cursor: default;
    opacity: 0.6;
}
.remainChar{
    color: rgb(170, 170, 170);
    position: fixed;
    font-size: 20px;
    bottom: 20px;
    right: 20px;
}

.startPage{
    height: 100%;
    .circleBack{
        height: 30px;
        width: 30px;
        right: 20px;
        top: 20px;
        background-color: rgb(230, 230, 230);
        border-radius: 50%;
        position: absolute;
        svg{
            height: 30px;
            width: 30px;
        }
    }
    .startContent{
        width: 100%;
        height: 717px;
        text-align: center;
        position: absolute;
        left: 50%;
        top: 40px;
        transform: translate(-50%, 0);
        ul{
            height: 260px;
            width: 414px;
            list-style: none;
            padding: 0px;
            font-size: 30px;
            font-weight: 500;
            li{
                height: 40px;
                margin-top: 15px;
            }
        }
        img{
            width: 414px;
            height: 333px;
        }
        button{
            margin-top: 10px;
        }
    }
}
