@import '../../../_global.scss';
@import '../HeadIndex/headIndex.scss';

.box{
  border: 1px solid rgb(240,240,240);
  background-color: $box-color;
  width: 356px;
  border-radius: 8px;
  margin: 0 auto;
  padding: 20px 10px 10px 10px;
  margin-bottom: 16px;
}
.box0{
  height: 80px;
}
.box1{
  height: 120px;
}
.box2{
  height: 150px;
}

.boxActivate{ //直接在box上动态添加:active会导致active的范围不对，所以重写了一遍
  @extend .box;
  &:active{
    background-color: #F0F0F0;
  }
  padding: 20px 10px 20px 10px;
}

.viewAll{
  @extend .dataNum;
  text-align: center;
  border-top: 1px solid rgb(220,220,220);
  padding: 10px 0 0px 0;
  margin: 15px -10px 0 -10px;
  &:active{
    background-color: #F0F0F0;
  }
}

