.tooltip{
    position: fixed;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    text-align: center;
    height: 50px;
    line-height: 50px;
    width: 200px;
    background-color: rgb(30, 30, 30);
    opacity: 0.7;
    font-size: 20px;
    color: white;
    border-radius: 10px;
    z-index: 15;

}