@import '../../_global.scss';

.operation{
    position: fixed;
    left: 0px;
    top: 0px;
    background-color: white;
    line-height: 30px;
    height: 30px;
    width: 100%;
    padding: 10px 0 10px 0;
    opacity: 0.8;
    span{
        margin-left: 15px;
        font-size: 16px;
    }
    .opSvg{
        height: 31px;
        width: 31px;
        position: absolute;
        right: 20px;
    }
    ul{
        width: 120px;
        float: right;
        margin-top: 42px; //30height + 10padding + 1border * 2 
        list-style: none;
        padding: 0;
        display: none;
        font-size: 16px;
        li{
            border: 1px solid rgb(225, 225, 225);
            padding: 10px;
            &:active{
                background-color: #F0F0F0;
            }
        }
    }
}
.index{
    position: relative;
    padding:200px 0 50px 0;
    background-color: $background-color;
}
.avatar{
    height: 70px;
    width: 70px;
    background-color: rgb(225, 225, 225);
    border-radius: 70px;
    margin: 0 auto;    
    margin-top: -65px;
}
.userName{
    text-align: center;
    font-size: 20px;
    margin-top: 15px;
}