@import '../HeadIndex/headIndex.scss';

.wishBox{
    padding: 10px 50px 10px 15px;
    border: 1px solid rgb(210, 210, 210);
    border-radius: 10px;
    margin: 13px 5px 13px 5px;
    position: relative;
    span{
        font-size: 16px;
        display: block;
    }
    .content{
        width: 100%;
        word-break: break-all;
    }
    .time{
        @extend .dataNum;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translate(0, -50%);
    }
}
